// Fixed InputField component - moved outside to prevent recreation
    const InputField: React.FC<InputFieldProps> = ({ 
        icon: Icon, 
        label, 
        name, 
        type = 'text', 
        placeholder, 
        required = false, 
        className = '', 
        ...props 
    }) => (
        <div className={className}>
            <label className="block text-sm font-medium text-gray-700 mb-1">
                {label} {required && <span className="text-red-500">*</span>}
            </label>
            <div className="relative">
                <div className="absolute inset-y-0 left-0 pl-3 flex items-center pointer-events-none">
                    <div className="bg-gray-100 p-1 rounded">
                        <Icon className="h-4 w-4 text-gray-500" />
                    </div>
                </div>
                <input
                    type={type}
                    name={name}
                    value={formData[name] || ''} // Always ensure string value
                    onChange={handleInputChange}
                    className={`w-full pl-12 pr-4 py-2 border-2 rounded-md transition-all duration-200 ${
                        errors[name] ? 'border-red-300 focus:border-red-500' : 'border-gray-300 focus:border-blue-500'
                    } focus:outline-none focus:ring-2 focus:ring-blue-200`}
                    placeholder={placeholder}
                    {...props}
                />
            </div>
            {errors[name] && <p className="text-red-500 text-sm mt-1">{errors[name]}</p>}
        </div>
    );

    // Fixed TextAreaField component
    const TextAreaField: React.FC<TextAreaFieldProps> = ({ 
        icon: Icon, 
        label, 
        name, 
        placeholder, 
        required = false, 
        className = '', 
        rows = 2 
    }) => (
        <div className={className}>
            <label className="block text-sm font-medium text-gray-700 mb-1">
                {label} {required && <span className="text-red-500">*</span>}
            </label>
            <div className="relative">
                <div className="absolute top-3 left-3 pointer-events-none">
                    <div className="bg-gray-100 p-1 rounded">
                        <Icon className="h-4 w-4 text-gray-500" />
                    </div>
                </div>
                <textarea
                    name={name}
                    value={formData[name] || ''} // Always ensure string value
                    onChange={handleInputChange}
                    rows={rows}
                    className={`w-full pl-12 pr-4 py-2 border-2 rounded-md transition-all duration-200 ${
                        errors[name] ? 'border-red-300 focus:border-red-500' : 'border-gray-300 focus:border-blue-500'
                    } focus:outline-none focus:ring-2 focus:ring-blue-200 resize-none`}
                    placeholder={placeholder}
                />
            </div>
            {errors[name] && <p className="text-red-500 text-sm mt-1">{errors[name]}</p>}
        </div>
    );'use client'
import React, { useState } from 'react';
import { Building2, User, MapPin, Phone, Mail, FileText, Percent, Hash, CreditCard, MessageCircle, Home, Navigation } from 'lucide-react';

interface FormData {
    name: string;
    accNo: string;
    address: string;
    location: string;
    post: string;
    pin: string;
    phone: string;
    whatsapp: string;
    email: string;
    type: string;
    gstNo: string;
    discPercentage: string;
    stopped: string;
    status: string;
}

interface FormErrors {
    [key: string]: string;
}

interface TypeOption {
    id: number;
    name: string;
}

interface InputFieldProps {
    icon: React.ComponentType<{ className?: string }>;
    label: string;
    name: keyof FormData;
    type?: string;
    placeholder: string;
    required?: boolean;
    className?: string;
    maxLength?: number;
    min?: string;
    max?: string;
    step?: string;
    style?: React.CSSProperties;
}

interface TextAreaFieldProps {
    icon: React.ComponentType<{ className?: string }>;
    label: string;
    name: keyof FormData;
    placeholder: string;
    required?: boolean;
    className?: string;
    rows?: number;
}

// Fixed InputField component - moved outside to prevent recreation
const InputField: React.FC<InputFieldProps & { 
    value: string; 
    onChange: (e: React.ChangeEvent<HTMLInputElement>) => void; 
    error?: string; 
}> = ({ 
    icon: Icon, 
    label, 
    name, 
    type = 'text', 
    placeholder, 
    required = false, 
    className = '', 
    value,
    onChange,
    error,
    ...props 
}) => (
    <div className={className}>
        <label className="block text-sm font-medium text-gray-700 mb-1">
            {label} {required && <span className="text-red-500">*</span>}
        </label>
        <div className="relative">
            <div className="absolute inset-y-0 left-0 pl-3 flex items-center pointer-events-none">
                <div className="bg-gray-100 p-1 rounded">
                    <Icon className="h-4 w-4 text-gray-500" />
                </div>
            </div>
            <input
                type={type}
                name={name}
                value={value}
                onChange={onChange}
                className={`w-full pl-12 pr-4 py-2 border-2 rounded-md transition-all duration-200 ${
                    error ? 'border-red-300 focus:border-red-500' : 'border-gray-300 focus:border-blue-500'
                } focus:outline-none focus:ring-2 focus:ring-blue-200`}
                placeholder={placeholder}
                {...props}
            />
        </div>
        {error && <p className="text-red-500 text-sm mt-1">{error}</p>}
    </div>
);

// Fixed TextAreaField component - moved outside to prevent recreation
const TextAreaField: React.FC<TextAreaFieldProps & { 
    value: string; 
    onChange: (e: React.ChangeEvent<HTMLTextAreaElement>) => void; 
    error?: string; 
}> = ({ 
    icon: Icon, 
    label, 
    name, 
    placeholder, 
    required = false, 
    className = '', 
    rows = 2,
    value,
    onChange,
    error
}) => (
    <div className={className}>
        <label className="block text-sm font-medium text-gray-700 mb-1">
            {label} {required && <span className="text-red-500">*</span>}
        </label>
        <div className="relative">
            <div className="absolute top-3 left-3 pointer-events-none">
                <div className="bg-gray-100 p-1 rounded">
                    <Icon className="h-4 w-4 text-gray-500" />
                </div>
            </div>
            <textarea
                name={name}
                value={value}
                onChange={onChange}
                rows={rows}
                className={`w-full pl-12 pr-4 py-2 border-2 rounded-md transition-all duration-200 ${
                    error ? 'border-red-300 focus:border-red-500' : 'border-gray-300 focus:border-blue-500'
                } focus:outline-none focus:ring-2 focus:ring-blue-200 resize-none`}
                placeholder={placeholder}
            />
        </div>
        {error && <p className="text-red-500 text-sm mt-1">{error}</p>}
    </div>
);

const SupplierForm = () => {
    const [formData, setFormData] = useState<FormData>({
        name: '',
        accNo: '',
        address: '',
        location: '',
        post: '',
        pin: '',
        phone: '',
        whatsapp: '',
        email: '',
        type: '',
        gstNo: '',
        discPercentage: '',
        stopped: 'N',
        status: '1'
    });

    const [errors, setErrors] = useState<FormErrors>({});
    const [isSubmitting, setIsSubmitting] = useState<boolean>(false);

    const [showTypeModal, setShowTypeModal] = useState(false);
    const [newType, setNewType] = useState('');
    const [types, setTypes] = useState<TypeOption[]>([
        { id: 1, name: 'Pharmaceutical' },
        { id: 2, name: 'Medical Equipment' },
        { id: 3, name: 'General Supplies' }
    ]);

    // Fixed input change handler - no more re-rendering issues
    const handleInputChange = (e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement>) => {
        const { name, value } = e.target;
        const fieldName = name as keyof FormData;
        
        setFormData(prev => ({ ...prev, [fieldName]: value }));

        // Clear error when user starts typing
        setErrors(prev => {
            if (prev[fieldName]) {
                const newErrors = { ...prev };
                delete newErrors[fieldName];
                return newErrors;
            }
            return prev;
        });
    };

    // Fixed select change handler - simplified logic
    const handleSelectChange = (e: React.ChangeEvent<HTMLSelectElement>) => {
        const value = e.target.value;
        setFormData(prev => ({ ...prev, type: value }));
        
        setErrors(prev => {
            if (prev.type) {
                const newErrors = { ...prev };
                delete newErrors.type;
                return newErrors;
            }
            return prev;
        });
    };

    const validateForm = (): boolean => {
        const newErrors: FormErrors = {};

        if (!formData.name.trim()) newErrors.name = 'Supplier name is required';
        if (!formData.accNo.trim()) newErrors.accNo = 'Supplier ID is required';
        if (!formData.address.trim()) newErrors.address = 'Address is required';
        if (!formData.location.trim()) newErrors.location = 'Location is required';
        if (!formData.pin.trim()) newErrors.pin = 'PIN code is required';
        if (!formData.phone.trim()) newErrors.phone = 'Phone number is required';
        if (!formData.type) newErrors.type = 'Supplier type is required';

        // Email validation
        const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
        if (formData.email && !emailRegex.test(formData.email)) {
            newErrors.email = 'Please enter a valid email address';
        }

        // Phone validation
        const phoneRegex = /^\d{10}$/;
        if (formData.phone && !phoneRegex.test(formData.phone.replace(/\D/g, ''))) {
            newErrors.phone = 'Please enter a valid 10-digit phone number';
        }

        // GST validation
        const gstRegex = /^[0-9]{2}[A-Z]{5}[0-9]{4}[A-Z]{1}[1-9A-Z]{1}Z[0-9A-Z]{1}$/;
        if (formData.gstNo && !gstRegex.test(formData.gstNo)) {
            newErrors.gstNo = 'Please enter a valid GST number';
        }

        // PIN validation
        const pinRegex = /^\d{6}$/;
        if (formData.pin && !pinRegex.test(formData.pin)) {
            newErrors.pin = 'Please enter a valid 6-digit PIN code';
        }

        setErrors(newErrors);
        return Object.keys(newErrors).length === 0;
    };

    const fetchTypes = async () => {
        try {
            const response = await fetch('https://localhost:7112/api/MedicineTypes');
            const data: { typeId: number; typeName: string }[] = await response.json();

            setTypes(data.map(t => ({
                id: t.typeId,
                name: t.typeName
            })));
        } catch (error) {
            console.error('Error fetching types:', error);
        }
    };

    const handleAddType = async () => {
        if (newType.trim()) {
            try {
                const response = await fetch('https://localhost:7112/api/MedicineTypes', {
                    method: 'POST',
                    headers: { 'Content-Type': 'application/json' },
                    body: JSON.stringify({ typeName: newType.trim() })
                });

                if (response.ok) {
                    const newTypeData = await response.json();

                    const newEntry = {
                        id: newTypeData.typeId,
                        name: newTypeData.typeName
                    };

                    await fetchTypes();

                    setFormData(prev => ({
                        ...prev,
                        type: newEntry.name
                    }));

                    setNewType('');
                    setShowTypeModal(false);
                } else {
                    const errText = await response.text();
                    console.error('Failed to add type:', errText);
                }
            } catch (error) {
                console.error('Error adding type:', error);
            }
        }
    };

    const handleSubmit = async (): Promise<void> => {
        if (!validateForm()) return;

        setIsSubmitting(true);

        try {
            // Simulate API call
            await new Promise(resolve => setTimeout(resolve, 1500));

            console.log('Form submitted:', formData);

            // Reset form on success
            setFormData({
                name: '',
                accNo: '',
                address: '',
                location: '',
                post: '',
                pin: '',
                phone: '',
                whatsapp: '',
                email: '',
                type: '',
                gstNo: '',
                discPercentage: '',
                stopped: 'N',
                status: '1'
            });

            alert('Supplier registered successfully!');
        } catch (error) {
            console.error('Error submitting form:', error);
            alert('Error submitting form. Please try again.');
        } finally {
            setIsSubmitting(false);
        }
    };

    // Fixed InputField component - ensures value is always a string
    const InputField: React.FC<InputFieldProps> = ({ 
        icon: Icon, 
        label, 
        name, 
        type = 'text', 
        placeholder, 
        required = false, 
        className = '', 
        ...props 
    }) => (
        <div className={className}>
            <label className="block text-sm font-medium text-gray-700 mb-1">
                {label} {required && <span className="text-red-500">*</span>}
            </label>
            <div className="relative">
                <div className="absolute inset-y-0 left-0 pl-3 flex items-center pointer-events-none">
                    <div className="bg-gray-100 p-1 rounded">
                        <Icon className="h-4 w-4 text-gray-500" />
                    </div>
                </div>
                <input
                    type={type}
                    name={name}
                    value={formData[name] || ''} // Always ensure string value
                    onChange={handleInputChange}
                    className={`w-full pl-12 pr-4 py-2 border-2 rounded-md transition-all duration-200 ${
                        errors[name] ? 'border-red-300 focus:border-red-500' : 'border-gray-300 focus:border-blue-500'
                    } focus:outline-none focus:ring-2 focus:ring-blue-200`}
                    placeholder={placeholder}
                    {...props}
                />
            </div>
            {errors[name] && <p className="text-red-500 text-sm mt-1">{errors[name]}</p>}
        </div>
    );

    // Fixed TextAreaField component
    const TextAreaField: React.FC<TextAreaFieldProps> = ({ 
        icon: Icon, 
        label, 
        name, 
        placeholder, 
        required = false, 
        className = '', 
        rows = 2 
    }) => (
        <div className={className}>
            <label className="block text-sm font-medium text-gray-700 mb-1">
                {label} {required && <span className="text-red-500">*</span>}
            </label>
            <div className="relative">
                <div className="absolute top-3 left-3 pointer-events-none">
                    <div className="bg-gray-100 p-1 rounded">
                        <Icon className="h-4 w-4 text-gray-500" />
                    </div>
                </div>
                <textarea
                    name={name}
                    value={formData[name] || ''} // Always ensure string value
                    onChange={handleInputChange}
                    rows={rows}
                    className={`w-full pl-12 pr-4 py-2 border-2 rounded-md transition-all duration-200 ${
                        errors[name] ? 'border-red-300 focus:border-red-500' : 'border-gray-300 focus:border-blue-500'
                    } focus:outline-none focus:ring-2 focus:ring-blue-200 resize-none`}
                    placeholder={placeholder}
                />
            </div>
            {errors[name] && <p className="text-red-500 text-sm mt-1">{errors[name]}</p>}
        </div>
    );

    return (
        <div className="min-h-screen bg-gray-50">
            {/* Header Section */}
            <div className="bg-gradient-to-r from-blue-600 to-purple-600 text-white py-12 px-8">
                <div className="text-center">
                    <h1 className="text-4xl font-bold mb-4">Supplier Registration</h1>
                    <p className="text-lg opacity-90">Fill in the details to register a new supplier</p>
                </div>
            </div>

            <div className="w-full px-8 py-8">
                {/* Form Container */}
                <div className="bg-white rounded-lg shadow-lg border border-gray-200 w-full">
                    <div className="p-8 space-y-8">
                        {/* Basic Information */}
                        <div className="space-y-4">
                            <h3 className="text-lg font-semibold text-gray-900 border-b border-gray-200 pb-2">
                                Basic Information
                            </h3>
                            
                            <div className="flex flex-col sm:flex-row gap-4">
                                <InputField
                                    icon={Hash}
                                    label="Supplier ID"
                                    name="accNo"
                                    placeholder="Enter supplier ID"
                                    required
                                    className="flex-1 sm:flex-none"
                                    style={{ flexBasis: '20%' }}
                                    value={formData.accNo}
                                    onChange={handleInputChange}
                                    error={errors.accNo}
                                />
                                <InputField
                                    icon={User}
                                    label="Supplier Name"
                                    name="name"
                                    placeholder="Enter supplier name"
                                    required
                                    className="flex-1"
                                    style={{ flexBasis: '45%' }}
                                    value={formData.name}
                                    onChange={handleInputChange}
                                    error={errors.name}
                                />
                                <div className="flex-1 sm:flex-none" style={{ flexBasis: '35%' }}>
                                    <label className="block text-sm font-medium text-gray-700 mb-1">
                                        Supplier Type <span className="text-red-500">*</span>
                                    </label>
                                    <div className="flex gap-2">
                                        <div className="relative flex-1">
                                            <div className="absolute inset-y-0 left-0 pl-3 flex items-center pointer-events-none">
                                                <div className="bg-gray-100 p-1 rounded">
                                                    <Building2 className="h-4 w-4 text-gray-500" />
                                                </div>
                                            </div>
                                            <select
                                                name="type"
                                                value={formData.type}
                                                onChange={handleSelectChange}
                                                className={`w-full pl-12 pr-4 py-2 border-2 rounded-md transition-all duration-200 ${
                                                    errors.type ? 'border-red-300 focus:border-red-500' : 'border-gray-300 focus:border-blue-500'
                                                } focus:outline-none focus:ring-2 focus:ring-blue-200`}
                                            >
                                                <option value="">Select Type</option>
                                                {types.map(type => (
                                                    <option key={type.id} value={type.name}>
                                                        {type.name}
                                                    </option>
                                                ))}
                                            </select>
                                        </div>
                                        <button
                                            type="button"
                                            onClick={() => setShowTypeModal(true)}
                                            className="bg-green-500 hover:bg-green-600 text-white px-3 py-2 rounded-md font-medium transition-colors"
                                        >
                                            +
                                        </button>
                                    </div>
                                    {errors.type && <p className="text-red-500 text-sm mt-1">{errors.type}</p>}
                                </div>
                            </div>
                        </div>

                        {/* Address Information */}
                        <div className="space-y-4">
                            <h3 className="text-lg font-semibold text-gray-900 border-b border-gray-200 pb-2">
                                Address Information
                            </h3>
                            
                            <TextAreaField
                                icon={Home}
                                label="Address"
                                name="address"
                                placeholder="Enter complete address"
                                required
                                className="w-full"
                                value={formData.address}
                                onChange={handleInputChange}
                                error={errors.address}
                            />
                            
                            <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
                                <InputField
                                    icon={MapPin}
                                    label="Location"
                                    name="location"
                                    placeholder="Enter city/location"
                                    required
                                    value={formData.location}
                                    onChange={handleInputChange}
                                    error={errors.location}
                                />
                                <InputField
                                    icon={Building2}
                                    label="Post Office"
                                    name="post"
                                    placeholder="Enter post office"
                                    value={formData.post}
                                    onChange={handleInputChange}
                                    error={errors.post}
                                />
                                <InputField
                                    icon={Navigation}
                                    label="PIN Code"
                                    name="pin"
                                    placeholder="Enter PIN code"
                                    required
                                    maxLength={6}
                                    value={formData.pin}
                                    onChange={handleInputChange}
                                    error={errors.pin}
                                />
                            </div>
                        </div>

                        {/* Contact Information */}
                        <div className="space-y-4">
                            <h3 className="text-lg font-semibold text-gray-900 border-b border-gray-200 pb-2">
                                Contact Information
                            </h3>
                            
                            <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
                                <InputField
                                    icon={Phone}
                                    label="Phone Number"
                                    name="phone"
                                    type="tel"
                                    placeholder="Enter phone number"
                                    required
                                    value={formData.phone}
                                    onChange={handleInputChange}
                                    error={errors.phone}
                                />
                                <InputField
                                    icon={MessageCircle}
                                    label="WhatsApp Number"
                                    name="whatsapp"
                                    type="tel"
                                    placeholder="Enter WhatsApp number"
                                    value={formData.whatsapp}
                                    onChange={handleInputChange}
                                    error={errors.whatsapp}
                                />
                                <InputField
                                    icon={Mail}
                                    label="Email Address"
                                    name="email"
                                    type="email"
                                    placeholder="Enter email address"
                                    value={formData.email}
                                    onChange={handleInputChange}
                                    error={errors.email}
                                />
                            </div>
                        </div>

                        {/* Business Information */}
                        <div className="space-y-4">
                            <h3 className="text-lg font-semibold text-gray-900 border-b border-gray-200 pb-2">
                                Business Information
                            </h3>
                            
                            <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                                <InputField
                                    icon={FileText}
                                    label="GST Number"
                                    name="gstNo"
                                    placeholder="Enter GST number"
                                    value={formData.gstNo}
                                    onChange={handleInputChange}
                                    error={errors.gstNo}
                                />
                                <InputField
                                    icon={Percent}
                                    label="Discount Percentage"
                                    name="discPercentage"
                                    type="number"
                                    placeholder="Enter discount %"
                                    min="0"
                                    max="100"
                                    step="0.01"
                                    value={formData.discPercentage}
                                    onChange={handleInputChange}
                                    error={errors.discPercentage}
                                />
                            </div>
                        </div>

                        {/* Submit Button */}
                        <div className="flex justify-center pt-6">
                            <button
                                type="button"
                                onClick={handleSubmit}
                                disabled={isSubmitting}
                                className="group relative px-8 py-3 bg-gradient-to-r from-blue-600 to-purple-600 text-white font-semibold rounded-lg shadow-lg hover:shadow-xl transform hover:scale-105 transition-all duration-300 disabled:opacity-50 disabled:cursor-not-allowed disabled:transform-none overflow-hidden"
                            >
                                <span className="absolute inset-0 bg-gradient-to-r from-purple-600 to-blue-600 transform scale-x-0 group-hover:scale-x-100 transition-transform duration-300 origin-left"></span>
                                <span className="relative flex items-center">
                                    {isSubmitting ? (
                                        <>
                                            <svg className="animate-spin -ml-1 mr-3 h-5 w-5 text-white" xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24">
                                                <circle className="opacity-25" cx="12" cy="12" r="10" stroke="currentColor" strokeWidth="4"></circle>
                                                <path className="opacity-75" fill="currentColor" d="M4 12a8 8 0 018-8V0C5.373 0 0 5.373 0 12h4zm2 5.291A7.962 7.962 0 014 12H0c0 3.042 1.135 5.824 3 7.938l3-2.647z"></path>
                                            </svg>
                                            Processing...
                                        </>
                                    ) : (
                                        <>
                                            <CreditCard className="mr-2 h-5 w-5" />
                                            Register Supplier
                                        </>
                                    )}
                                </span>
                            </button>
                        </div>
                    </div>
                </div>
            </div>

            {/* Type Modal */}
            {showTypeModal && (
                <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50">
                    <div className="bg-white rounded-lg shadow-xl w-full max-w-md mx-4">
                        <div className="bg-gray-600 text-white px-6 py-4 rounded-t-lg flex justify-between items-center">
                            <h2 className="text-lg font-medium">Add New Type</h2>
                            <button
                                onClick={() => setShowTypeModal(false)}
                                className="text-white hover:text-gray-300 text-xl font-bold"
                            >
                                Ã—
                            </button>
                        </div>
                        <div className="p-6">
                            <label className="block text-sm font-medium text-gray-700 mb-2">Type Name</label>
                            <input
                                type="text"
                                value={newType}
                                onChange={(e) => setNewType(e.target.value)}
                                placeholder="Enter type name"
                                className="w-full border border-gray-300 rounded px-3 py-2 text-sm focus:outline-none focus:ring-2 focus:ring-blue-500"
                                onKeyPress={(e) => e.key === 'Enter' && handleAddType()}
                            />
                        </div>
                        <div className="border-t border-gray-200 px-6 py-4 flex justify-end gap-3">
                            <button
                                onClick={() => setShowTypeModal(false)}
                                className="bg-gray-500 hover:bg-gray-600 text-white px-4 py-2 rounded text-sm font-medium transition-colors"
                            >
                                Cancel
                            </button>
                            <button
                                onClick={handleAddType}
                                className="bg-blue-500 hover:bg-blue-600 text-white px-4 py-2 rounded text-sm font-medium transition-colors"
                            >
                                Add
                            </button>
                        </div>
                    </div>
                </div>
            )}
        </div>
    );
};

export default SupplierForm;